<template>
  <div id="view_employee">
      <ul class="collection with-header">
          <li class="collection-header"><h4>Name:{{name}}</h4></li>
          <li class="collection-item">Employee ID:{{employee_id}}</li>
          <li class="collection-item">Department:{{dept}}</li>
          <li class="collection-item">Position:{{position}}</li>
      </ul>
      <router-link to="/" class="btn-large grey">Back</router-link> 
      <button @click="deleteEmployee" class="btn-large red">Delete</button>
      <div class="fixed-action-btn">
          <router-link v-bind:to="{name: 'edit_employee', params: {employee_id: employee_id}}" class="btn-floating btn-large red">
            <i class="fa fa-pencil" aria-hidden="true"></i>
          </router-link>
      </div>
  </div>    
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view_employee',
    data(){
        return{
            employee_id: null,
            name: null,
            position: null,
            dept: null            
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id)
        .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.position = doc.data().position
                    vm.dept = doc.data().dept
                })
                  
            })
        })
    },
    watch: { '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.collection('employees').where
            ('employee_id', '==', 
            this.$route.params.employee_id).get()
            .then(
                querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employee_id
                        this.name = doc.data().name
                        this.position = doc.data().position
                        this.dept = doc.data().dept

                    })
                }
            )
        },
        deleteEmployee(){
            if(confirm('Are you sure')) {
                db.collection('employees').where(
                    'employee_id', '==', 
                this.$route.params.employee_id).get()
                .then(
                    querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete()
                            this.$router.push('/')
                        
                    })
                }
            )
                
            }
        }
    }

}
</script>

<style>

</style>template>


