<template>
  <div id="edit_employee">
      <h3>Edit Employee</h3>
      <div class="row">
          <form @submit.prevent="updateEmployee">
          <div class="input-field col s12">
              <input type="text" v-model="employee_id" disabled>
              <label class="active">Employee ID</label>
          </div>
          <div class="input-field col s12">
              <input type="text" v-model="name" required>
              <label class="active">Name</label>
          </div>
          <div class="input-field col s12">
              <input type="text" v-model="position" required>
              <label class="active">Position</label>
          </div>
          <div class="input-field col s12">
              <input type="text" v-model="dept" required>
              <label class="active">Department</label>
          </div>
          </form>
          <button @click="updateEmployee" type="submit" class="btn blue">Submit</button>
      </div>
  </div>    
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit_employee',
    data(){
        return{
            employee_id: null,
            name: null,
            position: null,
            dept: null            
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id)
        .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.position = doc.data().position
                    vm.dept = doc.data().dept
                })
                  
            })
        })
    },
    watch: { '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.collection('employees').where
            ('employee_id', '==', 
            this.$route.params.employee_id).get()
            .then(
                querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employee_id
                        this.name = doc.data().name
                        this.position = doc.data().position
                        this.dept = doc.data().dept

                    })
                }
            )
        },
        updateEmployee () {
            db.collection('employees').where
            ('employee_id', '==', 
            this.$route.params.employee_id).get()
            .then(
                querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            employee_id: this.employee_id,
                            name: this.name,
                            dept: this.dept,
                            position: this.position
                        })
                        .then(() => {
                            this.$router.push({name: 'view_employee', 
                            params: {employee_id: this.employee_id}
                            })
                        })
                    })
                }
            )
        }
        
    }

}
</script>

<style>

</style>template>


